export default {
	data() {
		return {
			baseUrl: getApp().globalData.baseUrl,
			fileUrl: getApp().globalData.fileUrl,
			followList: [],
			isEmpty: false,
			avatarUrl: '/static/images/avatar.jpg',
		}
	},
	props: ['userId'],
	mounted() {
		let user = getApp().globalData.getGlobalUserInfo()
		uni.request({
			url: this.baseUrl + '/user/queryFollows?userId=' + this.userId,
			method: 'POST',
			header: {
				'content-type': 'application/json',
				'userId': user.id,
				'userToken': user.userToken
			},
			success: (res) => {
				if (res.data.status === 200) {
					this.followList = res.data.data
					if (res.data.data.length === 0) {
						this.isEmpty = true
					}
				}
			}
		})
	},
	methods:{
		toPersonPage(userId) {
			uni.navigateTo({
				url: '../../pages/person/person?userId=' + userId
			})
		}
	}
}
